.modal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
    outline: none;
    background-color: rgba(0, 0, 0, .5);
    transform-origin: top;
    transform: scaleY(0);
    z-index: 1000;
    transition: transform .3s ease-in-out;
    &__dialog {
        max-width: 308px;
        margin: 10px auto;
    }
    &__content {
        padding: 24px;
        background-color: var(--bg-color);
        border-radius: 12px;
        position: relative;
    }

    .modal__close-btn {
        position: absolute;
        top: 10px;
        right: 8px;
    }

    &__header {
        display: flex;
        align-items: center;
        padding-bottom: 6px;
        border-bottom: 1px solid var(--gray-regular);
        &-img {
            margin-right: 8px;
        }
    }

    &__body {
        padding-top: 8px;
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 5px;
        }
        &-bottom {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &-descr {
            margin-bottom: 16px;
        }
    }
    &__btn-add {
        height: 100%;
        min-height: 28px;
        border-radius: 8px;
        background-color: var(--red);
        color: var(--white);
        font-size: 1.25rem;
        line-height: 1.5rem;
        font-weight: 500;
        flex-basis: 48.4%;
    }
    &__count {
        height: 100%;
        min-height: 28px;
        border-radius: 8px;
        border: 1px solid var(--gray-regular);
        flex-basis: 48.4%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1.5px;
    }
    &__btn {
        position: relative;
        width: 24px;
        height: 24px;
        border-radius: 6px;
        border: 1px solid var(--gray-regular);
        transition: background-color .3s ease-in-out, border-color .3s ease-in-out;
        &:active {
            background-color: var(--yollow);
            border-color: var(--yollow);
        }

        &::after {
          content: '';
          position: absolute;
          top: calc(50% - .75px);
          left: calc(50% - 4px);
          width: 8px;
          height: 1.5px;
          border-radius: 2px;
          background-color: var(--text-color);
        }



        &-plus {
            &::before {
                content: '';
                position: absolute;
                top: calc(50% - .5px);
                left: calc(50% - 4.7px);
                width: 8px;
                height: 1.5px;
                border-radius: 2px;
                background-color: var(--text-color);
                transform: rotate(90deg);
            }
        }
    }
}

.modal.active-modal {
    transform: scaleY(1);
}

@media (max-width: 330px) {
    .modal__dialog {
        margin: 10px;
    }
}
